import Body from "./Components/Helpers/Body";
import { LineChart } from "./Components/LineChart";
import AppContext from "../Context/AppContext";
import { useContext, useState } from "react";

const Analysis = () => {
    const { accounts, } = useContext(AppContext);
    const [selectedAccount, setSelecetedAccount] = useState(accounts[0]);
    function accountSelectHandler(e) {
        console.log(e)
        setSelecetedAccount(JSON.parse(e.target.value))
    }
    return (

        <div className="content">
            <h1>Analysis</h1>
            <div className="content_display">
                <Body className="Analysis">
                    <select onChange={(e) => accountSelectHandler(e)}>
                        {
                            accounts.length > 0 ? accounts.map((a, key) => {
                                return <option key={key} value={JSON.stringify(a)}>{a.name}</option>
                            }) : ""
                        }
                    </select>
                    {
                        accounts.length > 0 ? <LineChart account={selectedAccount} /> : ""
                    }

                </Body>
            </div>
        </div>

    );

}

export default Analysis;

