import React, { useContext } from 'react';
import AppContext from "../../Context/AppContext";
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
);

export function LineChart({ account }) {
    console.log(account)

    const { accounts, } = useContext(AppContext);
    const graphTitle = account.name;
    const options = {
        responsive: true,
        plugins: {
            legend: {
                position: 'top',
            },
            title: {
                display: true,
                text: `Linechart Statistics`,
            },
        },
    };

    //this will be replaced


    const processedData = account.transactions.map(i => {
        //new Date(i.date)
        return {
            amount: i.amount,
            date: new Date(i.date),
            type: i.type
        }
    })



    processedData.sort((a, b) => {
        if (a.date < b.date)
            return -1
        else if (a.date > b.date)
            return 1;
        return 0;

    });



    const allDates = processedData.map(i => {
        return createLabels(i.date)
    })
    const allData = processedData.map(i => (i.type === 'Income') ? i.amount * 1 : i.amount * -1);

    const allProcessedData = accounts.map(a => {
        //new Date(i.date)

        let transactions = a.transactions.map(i => {
            //new Date(i.date)

            return {
                amount: i.amount,
                date: new Date(i.date),
                type: i.type
            }
        })

        return {
            name: a.name,
            transaction: transactions
        }


    })

    allProcessedData.sort((a, b) => {
        if (a.date < b.date)
            return -1
        else if (a.date > b.date)
            return 1;
        return 0;

    });


    // console.log(allProcessedData)
    const gatheredDates = allProcessedData.map(a => {

        let date = a.transaction.map(i => {
            //new Date(i.date)

            return createLabels(i.date)
        })
        return date;
    })

    let TotalDates = [...gatheredDates];
    //  console.log(TotalDates)

    function createLabels(dateObj) {

        let month = dateObj.getUTCMonth() + 1; //months from 1-12
        let day = dateObj.getUTCDate();
        let year = dateObj.getUTCFullYear();
        let newdate = year + "-" + month + "-" + day;
        return newdate;
    }

    const labels = allDates;

    const data = {
        labels,
        datasets: [
            {
                label: graphTitle,
                data: allData,
                borderColor: 'rgb(255, 99, 132)',
                backgroundColor: 'rgba(255, 99, 132, 0.5)',
            },

        ],
    };

    return <Line options={options} data={data} />;
}
